.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  background-color: #efeef1;
}

.hoverEffect:hover {
  border: 2px solid transparent;
  color: rebeccapurple;
}
.raduis {
  border-radius: 12%;
}
.card .title {
  padding: 15px 0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  /* width: 100%;
  height: 100%; */
  margin: 0;
  padding: 0;
  pointer-events: none;
}
